import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Cookies from 'js-cookie';
import { useNavigate } from 'react-router-dom'; 

const API_BASE_URL = process.env.REACT_APP_API_URL || "http://13.208.47.23:8911/api";

const SellerApplication = () => {
  const [form, setForm] = useState({
    brand_id: '',
    member_id: '',
    brandname: '',
    brand_number: '',
  });

  const [brandLogoFile, setBrandLogoFile] = useState(null);
  const [brandLogoPreview, setBrandLogoPreview] = useState(null);
  const [brandLicenseFile, setBrandLicenseFile] = useState(null);

  const [BrandNumberCheckResult, setBrandNumberCheckResult] = useState('');
  const [isSubmitDisabled, setIsSubmitDisabled] = useState(false);
  
  const navigate = useNavigate();
  
  // brand_id 
  const generateUniqueId = () => {
    return ((Date.now() % 100000) * (Math.floor(Math.random() * 9) + 1)); 
  };

  useEffect(() => {
    const uniqueId = generateUniqueId();
    setForm(prevForm => ({ 
      ...prevForm,
      brand_id: uniqueId,
    }));
  }, []);

  //   
  useEffect(() => {
    const token = Cookies.get('jwt_token'); 
    
    if (token) {
      axios.get(`${API_BASE_URL}/userinfo`, { 
        headers: { 'Authorization': `Bearer ${token}` }, 
        withCredentials: true
      })
      .then(response => {
        const brand_id = response.data.brand_id;
        const brand_status = response.data.brand_status;
        if (brand_id) {
          if(brand_status === "") {
            alert('   .   .');
            return navigate(`/brand/${brand_id}`);
          } else if (brand_status === " ") {
            alert('    .')
          } else {
            alert('   .  .')
          }
            navigate('/');
        }
        setForm(prevForm => ({
          ...prevForm,
          member_id: response.data.member_id,
        }));
      })
      .catch(error => {
        alert(error.response.data.message);
        Cookies.remove('jwt_token');
        navigate('/login');
      });
    } else {
      navigate('/login');
    }
  }, [navigate]);

  // 
  const checkBrandNumber = async (e) => {
    const enteredBrandNumber = e.target.value;
    setForm({ ...form, brand_number: enteredBrandNumber });

    if (enteredBrandNumber.length === 0) {
      setBrandNumberCheckResult('');
      return;
    }

    try {
      const response = await axios.get(`${API_BASE_URL}/checkBrandNumber`, {
        params: { brand_number: enteredBrandNumber },
      });

      setBrandNumberCheckResult(response.data);
      if (response.data === "  .") {
        setIsSubmitDisabled(true);
      } else {
        setIsSubmitDisabled(false);
      }
    } catch (error) {
      console.error('    ', error);
    }
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!brandLogoFile || !brandLicenseFile) {
      alert('      .');
      return;
    }

    const formData = new FormData();
    formData.append('brand_id', form.brand_id);
    formData.append('member_id', form.member_id);
    formData.append('brandname', form.brandname);
    formData.append('brand_number', form.brand_number);
    formData.append('brandlogo_url', brandLogoFile);
    formData.append('brandlicense_url', brandLicenseFile); 

    try {
      const response = await axios.post(`${API_BASE_URL}/sellApplication`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      if (response.status === 200) {
        alert('  .');
        navigate("/");
      } else {
        alert(' .   .');
      }
    } catch (error) {
      alert(' .     .');
    }
  };

  // 
  const handleBrandLogoChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setBrandLogoFile(file);
      
      const reader = new FileReader();
      reader.onloadend = () => {
        setBrandLogoPreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div className="join-form-container">
      <div className="join-flow">
        <div className="join-flow-title"> </div>
      </div>
      <hr></hr>

      <form className="join-form" onSubmit={handleSubmit}>
        <div className="join-form-title">
             <hr></hr>
        </div>

        <div>
          <input type="hidden" name="brand_id" value={form.brand_id} />
          <input type="hidden" name="member_id" value={form.member_id} />
        </div>

        <div>
          <input type="text" id="brandName" value={form.brandname} onChange={(e) => setForm({ ...form, brandname: e.target.value })} maxLength={100} required placeholder=" "/>
        </div>
        <div>
          <input type="text" id="brandNumber" value={form.brand_number} onChange={checkBrandNumber} placeholder="'-'    10 ." pattern="\d{10}" title="'-'    10 ." maxLength={10} required />
          {form.brand_number.length === 10 && <div>{BrandNumberCheckResult}</div>}
        </div>
        <br></br>
        <div>
          <label htmlFor="brandLicenseFile"> : </label>
          <div>
          {brandLogoPreview && <img src={brandLogoPreview} style = {{ width: '200px' , height: '200px'}} alt="  "/>}
          </div>
          <input type="file" id="brandLogoFile" accept="image/*" onChange={handleBrandLogoChange} required />
        </div>
        <br></br>
        <div>
          <label htmlFor="brandLicenseFile"> : </label>
          <input type="file" id="brandLicenseFile" accept="application/pdf" onChange={(e) => setBrandLicenseFile(e.target.files[0])} required />
        </div>
        <div className="join-button-form">
          <button type="submit" className="join-join" disabled={isSubmitDisabled}> </button>
        </div>
      </form>
    </div>
  );
};

export default SellerApplication;