import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import '../../../styles/FindIdPw.css';

const ResetPw = () => {
    const [username, setUsername] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');

    const navigate = useNavigate(); 
    const location = useLocation();

    const API_BASE_URL = process.env.REACT_APP_API_URL || "http://13.208.47.23:8911/api";

    //      
    useEffect(() => {
        const params = new URLSearchParams(location.search);
        const usernameFromUrl = params.get('username');

        const isFindPwValid = sessionStorage.getItem('findPwValid');

        if (!isFindPwValid) {
            alert(' .    .');
            navigate('/login/findPw'); 
            sessionStorage.removeItem('findPwValid');
            return;
        }

        if (usernameFromUrl) {
            setUsername(usernameFromUrl);
        } else {
            alert('  .  .');
            navigate('/login/findPw'); 
            sessionStorage.removeItem('findPwValid');
        }

        sessionStorage.removeItem('findPwValid');
        
    }, [location]);

    // 
    const handleChangePassword = () => {
        if (newPassword.trim() === '') {
            alert('  .');
            return;
        }

        if (newPassword !== confirmPassword) {
            alert('  .');
            return;
        }
        
        axios.post(`${API_BASE_URL}/resetPw`, {
            username,
            newPassword
        })
        .then(response => {
            alert(response.data.message); 
            navigate('/login'); 
        })
        .catch(error => {
            alert(error.response.data.message);  
        });
    };

    return (
        <div className="id-pw-find-container">
            <div className="find-flow">
                <div className="find-title">ID/PW</div>
            </div>
            <hr></hr>
            <div className="id-find-title">
                 <hr></hr>
            </div>
            <input type="password" placeholder="  " value={newPassword} onChange={(e) => setNewPassword(e.target.value)} maxLength={100} />
            <input type="password" placeholder=" " value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} maxLength={100} />
            <hr></hr>
            <button className="pw-find-button" onClick={handleChangePassword}> </button>
        </div>
    );
};

export default ResetPw;