import React, { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import Cookies from 'js-cookie';

const API_BASE_URL = process.env.REACT_APP_API_URL || "http://13.208.47.23:8911/api";

const styles = {
  container: {
    maxWidth: '900px',
    margin: '20px auto', 
    backgroundColor: '#fff',
    fontFamily: 'Arial, sans-serif'
  },
  heading: {
    fontSize: '40px',
    fontWeight: 'bold',
    textAlign: 'center',
    margin: '30px 0',
    borderBottom: '1px solid #eee',
    paddingBottom: '15px'
  },
  sectionTitle: {
    fontSize: '28px',
    fontWeight: 'bold',
    backgroundColor: '#f8f9fa',
    padding: '10px 15px',
    borderBottom: '1px solid #ddd',
    marginBottom: '15px'
  },
  card: {
    marginBottom: '25px',
    border: '1px solid #ddd',
    borderRadius: '4px',
    overflow: 'hidden'
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse'
  },
  tableHeader: {
    textAlign: 'left',
    padding: '10px',
    borderBottom: '1px solid #ddd',
    backgroundColor: '#f8f9fa',
    fontSize: '24px'
  },
  tableCell: {
    padding: '10px',
    borderBottom: '1px solid #eee',
    fontSize: '20px'
  },
  productImage: {
    width: '60px',
    height: '60px',
    objectFit: 'cover',
    borderRadius: '4px'
  },
  formRow: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '15px'
  },
  label: {
    width: '150px',
    textAlign: 'left',
    fontSize: '20px',
    color: '#333',
    padding: '0 15px 0 0'
  },
  formField: {
    flex: 1,
    border: '1px solid #ddd',
    padding: '8px 12px',
    borderRadius: '4px',
    fontSize: '20px'
  },
  select: {
    border: '1px solid #ddd',
    padding: '8px 12px',
    borderRadius: '4px',
    fontSize: '20px',
    width: '100%',
    backgroundColor: '#fff'
  },
  submitBtn: {
    backgroundColor: '#0088cc',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    padding: '10px 20px',
    fontSize: '22px',
    fontWeight: 'bold',
    cursor: 'pointer',
  },
  backBtn: {
    backgroundColor: '#6c757d',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    padding: '10px 20px',
    fontSize: '22px',
    cursor: 'pointer',
  },
  radioContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    gap: '20px'
  },
  radioLabel: {
    display: 'flex',
    alignItems: 'center',
    cursor: 'pointer',
    fontSize: '20px'
  },
  radioInput: {
    marginRight: '8px'
  },
  summaryBox: {
    backgroundColor: '#f8f9fa',
    padding: '15px',
    marginTop: '15px',
    borderRadius: '4px',
    border: '1px solid #ddd'
  },
  priceRow: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    margin: '10px 0'
  },
  priceLabel: {
    fontSize: '24px'
  },
  priceValue: {
    fontSize: '24px',
    fontWeight: 'bold'
  },
  totalRow: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    margin: '10px 0',
    borderTop: '1px solid #ddd',
    paddingTop: '10px'
  },
  totalLabel: {
    fontSize: '26px',
    fontWeight: 'bold'
  },
  totalValue: {
    fontSize: '30px',
    fontWeight: 'bold',
    color: '#0066cc'
  },
  mileageInfo: {
    fontSize: '18px',
    color: '#666',
    textAlign: 'right',
    marginTop: '5px'
  }
};

const OrderComponent = () => {
    const location = useLocation();
    const selectedCartItems = location.state?.selectedCartItems || [];
    const navigate = useNavigate();

    const [shippingMessage, setShippingMessage] = useState("");
    const [paymentMethod, setPaymentMethod] = useState("");
    const [address, setAddress] = useState("");
    const [receiverName, setReceiverName] = useState("");
    const [receiverPhone, setReceiverPhone] = useState("");
    const [receiverEmail, setReceiverEmail] = useState("");
    const [memberPoints, setMemberPoints] = useState(0);
    const [usedPoints, setUsedPoints] = useState(0);
    const [totalAmount, setTotalAmount] = useState(0);
    const [userInfo, setUserInfo] = useState(null);

    const getTokenFromCookie = () => {
        return Cookies.get('jwt_token') || null;
    };

    useEffect(() => {
        const fetchUserInfo = async () => {
            const token = getTokenFromCookie();
            if (token) {
                try {
                    const response = await axios.get(`${API_BASE_URL}/userinfo`, {
                        headers: { 'Authorization': `Bearer ${token}` },
                        withCredentials: true
                    });
                    
                    setUserInfo(response.data);
                    setMemberPoints(Number(response.data.points) || 0);
                    
                    if (response.data.realname) {
                        setReceiverName(response.data.realname);
                    }
                    
                    if (response.data.phone) {
                        setReceiverPhone(response.data.phone);
                    }
                    
                    if (response.data.email) {
                        setReceiverEmail(response.data.email);
                    }
                    
                    if (response.data.address) {
                        setAddress(response.data.address);
                    }
                    
                } catch (error) {
                    console.error("   :", error);
                    alert("   .");
                }
            }
        };
        
        fetchUserInfo();
    }, []);

    useEffect(() => {
        const total = selectedCartItems.reduce((sum, item) => sum + item.totalPrice, 0);
        setTotalAmount(total);
    }, [selectedCartItems]);

    const handleUsedPointsChange = (e) => {
        const value = Math.min(Math.max(0, parseInt(e.target.value) || 0), memberPoints);
        const maxUsablePoints = Math.min(memberPoints, totalAmount);
        setUsedPoints(Math.min(value, maxUsablePoints));
    };

    const handleOrderSubmit = async () => {
        if (!receiverName) {
            alert("  .");
            return;
        }
        if (!address) {
            alert(" .");
            return;
        }
        if (!receiverPhone) {
            alert(" .");
            return;
        }
        const token = getTokenFromCookie();
        if (!token) {
            alert(" .");
            navigate("/login");
            return;
        }
        if (selectedCartItems.length === 0) {
            alert("  .");
            navigate("/cart");
            return;
        }

        const orderData = {
            shippingAddress: address,
            shippingMessage,
            paymentMethod,
            receiverName,
            receiverPhone,
            receiverEmail,
            productInfo: selectedCartItems.map(item => ({
                productId: item.productId,
                quantity: item.quantity,
            })),
            usedPoints: Number(usedPoints),
        };

        console.log("Order Data Sent to Backend:", orderData);

        try {
            const response = await axios.post(
                `${API_BASE_URL}/buy/orderid`, 
                orderData,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${token}`,
                    },
                    withCredentials: true
                }
            );
            console.log(" :", response.data);
            alert(" .");
            navigate("/my/order");
        } catch (error) {
            console.error("  :", error);
            if (error.response) {
                alert(` .  : ${error.response.status}, : ${error.response.data || '   '}`);
            } else if (error.request) {
                alert("  .    .");
            } else {
                alert(`   : ${error.message}`);
            }
        }
    };

    const handlePhoneChange = (e) => {
        const value = e.target.value.replace(/[^0-9]/g, '');
        if (value.length <= 11) {
            let formattedValue = value;
            if (value.length > 3 && value.length <= 7) {
                formattedValue = value.replace(/(\d{3})(\d+)/, '$1-$2');
            } else if (value.length > 7) {
                formattedValue = value.replace(/(\d{3})(\d{4})(\d+)/, '$1-$2-$3');
            }
            setReceiverPhone(formattedValue);
        }
    };

    const remainingAmount = totalAmount - usedPoints;

    return (
        <div style={styles.container}>
            <h2 style={styles.heading}> </h2>
            
            {selectedCartItems.length === 0 ? (
                <div style={{ padding: '20px', backgroundColor: '#f8f9fa', textAlign: 'center', marginBottom: '20px' }}>
                    <p>  .</p>
                    <button 
                        onClick={() => navigate("/cart")}
                        style={styles.backBtn}
                    >
                         
                    </button>
                </div>
            ) : (
                <>
                    <div style={styles.card}>
                        <div style={styles.sectionTitle}></div>
                        <div style={{ padding: '10px' }}>
                            <table style={styles.table}>
                                <thead>
                                    <tr>
                                        <th style={{ ...styles.tableHeader, width: '50%' }}>/ </th>
                                        <th style={{ ...styles.tableHeader, width: '10%', textAlign: 'center' }}></th>
                                        <th style={{ ...styles.tableHeader, width: '20%', textAlign: 'right' }}></th>
                                        <th style={{ ...styles.tableHeader, width: '20%', textAlign: 'right' }}></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {selectedCartItems.map((item, index) => (
                                        <tr key={index}>
                                            <td style={styles.tableCell}>
                                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                                    <img 
                                                        src={`${API_BASE_URL}/showimage?filename=${item.image}&obj=product`} 
                                                        alt={item.productName || ' '} 
                                                        style={styles.productImage}
                                                    />
                                                    <div style={{ marginLeft: '10px' }}>
                                                        {item.productName || ` ID: ${item.productId}`}
                                                    </div>
                                                </div>
                                            </td>
                                            <td style={{ ...styles.tableCell, textAlign: 'center' }}>{item.quantity}</td>
                                            <td style={{ ...styles.tableCell, textAlign: 'right' }}>{item.price ? item.price.toLocaleString() : (item.totalPrice / item.quantity).toLocaleString()}</td>
                                            <td style={{ ...styles.tableCell, textAlign: 'right', fontWeight: 'bold' }}>{item.totalPrice.toLocaleString()}</td>
                                        </tr>
                                    ))}
                                </tbody>
                                <tfoot>
                                    <tr style={{ backgroundColor: '#f8f9fa' }}>
                                        <td colSpan="3" style={{ ...styles.tableCell, textAlign: 'right', fontWeight: 'bold' }}>:</td>
                                        <td style={{ ...styles.tableCell, textAlign: 'right', fontWeight: 'bold' }}>{totalAmount.toLocaleString()}</td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                    </div>

                    <div style={styles.card}>
                        <div style={styles.sectionTitle}> </div>
                        <div style={{ padding: '15px' }}>
                            <div style={styles.formRow}>
                                <label style={styles.label}> </label>
                                <input
                                    type="text"
                                    style={{ ...styles.formField, backgroundColor: '#f9f9f9' }}
                                    value={`${memberPoints.toLocaleString()}`}
                                    readOnly
                                />
                            </div>
                            <div style={styles.formRow}>
                                <label style={styles.label}> </label>
                                <input
                                    type="number"
                                    style={styles.formField}
                                    value={usedPoints}
                                    onChange={handleUsedPointsChange}
                                    min="0"
                                    max={Math.min(memberPoints, totalAmount)}
                                    placeholder="  "
                                />
                            </div>
                            <div style={{ fontSize: '18px', color: '#666', padding: '0 0 10px 150px' }}>
                                   : {Math.min(memberPoints, totalAmount).toLocaleString()}
                            </div>
                            <div style={styles.formRow}>
                                <label style={styles.label}>  </label>
                                <input
                                    type="text"
                                    style={{ ...styles.formField, backgroundColor: '#f9f9f9', fontWeight: 'bold' }}
                                    value={`${remainingAmount.toLocaleString()}`}
                                    readOnly
                                />
                            </div>
                        </div>
                    </div>

                    <div style={styles.card}>
                        <div style={styles.sectionTitle}> </div>
                        <div style={{ padding: '15px' }}>
                            <div style={styles.formRow}>
                                <label style={styles.label}>  <span style={{ color: 'red' }}>*</span></label>
                                <input
                                    type="text"
                                    style={styles.formField}
                                    value={receiverName}
                                    onChange={(e) => setReceiverName(e.target.value)}
                                    placeholder="   "
                                    required
                                />
                            </div>
                            <div style={styles.formRow}>
                                <label style={styles.label}> <span style={{ color: 'red' }}>*</span></label>
                                <input
                                    type="text"
                                    style={styles.formField}
                                    value={address}
                                    onChange={(e) => setAddress(e.target.value)}
                                    placeholder=" "
                                    required
                                />
                            </div>
                            <div style={styles.formRow}>
                                <label style={styles.label}> <span style={{ color: 'red' }}>*</span></label>
                                <input
                                    type="text"
                                    style={styles.formField}
                                    value={receiverPhone}
                                    onChange={handlePhoneChange}
                                    placeholder="   (: 010-1234-5678)"
                                    required
                                />
                            </div>
                            <div style={styles.formRow}>
                                <label style={styles.label}> <span style={{ color: 'red' }}>*</span></label>
                                <input
                                    type="email"
                                    style={styles.formField}
                                    value={receiverEmail}
                                    onChange={(e) => setReceiverEmail(e.target.value)}
                                    placeholder=" "
                                />
                            </div>
                            <div style={styles.formRow}>
                                <label style={styles.label}> </label>
                                <select
                                    style={styles.select}
                                    value={shippingMessage}
                                    onChange={(e) => setShippingMessage(e.target.value)}
                                >
                                    <option value=""></option>
                                    <option value="   ">   </option>
                                    <option value="   ">   </option>
                                    <option value="    ">    </option>
                                    <option value="  ">  </option>
                                    <option value=" "> </option>
                                </select>
                            </div>
                        </div>
                    </div>

                    <div style={styles.card}>
                        <div style={styles.sectionTitle}> </div>
                        <div style={{ padding: '15px' }}>
                            <div style={{ marginBottom: '15px' }}> </div>
                            
                            <div style={styles.radioContainer}>
                                <label style={styles.radioLabel}>
                                    <input
                                        type="radio"
                                        style={styles.radioInput}
                                        name="paymentMethod"
                                        value=""
                                        checked={paymentMethod === ""}
                                        onChange={(e) => setPaymentMethod(e.target.value)}
                                    />
                                    
                                </label>
                                <label style={styles.radioLabel}>
                                    <input
                                        type="radio"
                                        style={styles.radioInput}
                                        name="paymentMethod"
                                        value=""
                                        checked={paymentMethod === ""}
                                        onChange={(e) => setPaymentMethod(e.target.value)}
                                    />
                                    
                                </label>
                                <label style={styles.radioLabel}>
                                    <input
                                        type="radio"
                                        style={styles.radioInput}
                                        name="paymentMethod"
                                        value=""
                                        checked={paymentMethod === ""}
                                        onChange={(e) => setPaymentMethod(e.target.value)}
                                    />
                                    
                                </label>
                                <label style={styles.radioLabel}>
                                    <input
                                        type="radio"
                                        style={styles.radioInput}
                                        name="paymentMethod"
                                        value=""
                                        checked={paymentMethod === ""}
                                        onChange={(e) => setPaymentMethod(e.target.value)}
                                    />
                                    
                                </label>
                            </div>
                            
                            <div style={styles.summaryBox}>
                                <div style={styles.priceRow}>
                                    <div style={styles.priceLabel}></div>
                                    <div style={styles.priceValue}>{totalAmount.toLocaleString()}</div>
                                </div>
                                <div style={styles.priceRow}>
                                    <div style={styles.priceLabel}></div>
                                    <div style={styles.priceValue}>-{usedPoints.toLocaleString()}</div>
                                </div>
                                <div style={styles.totalRow}>
                                    <div style={styles.totalLabel}></div>
                                    <div style={styles.totalValue}>{remainingAmount.toLocaleString()}</div>
                                </div>
                                <div style={styles.mileageInfo}>
                                      : {usedPoints > 0 ? 0 : Math.floor(totalAmount * 0.05).toLocaleString()} 
                                </div>
                            </div>
                        </div>
                    </div>

                    <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '30px' }}>
                        <button 
                            style={styles.backBtn} 
                            onClick={() => navigate("/cart")}
                        >
                             
                        </button>
                        <button 
                            style={styles.submitBtn} 
                            onClick={handleOrderSubmit}
                        >
                            
                        </button>
                    </div>
                </>
            )}
        </div>
    );
};

export default OrderComponent;