import React, { useState, useEffect } from 'react';
import axios from 'axios';  
import { useNavigate } from 'react-router-dom';  
import Cookies from 'js-cookie';
import MyPageSideBar from '../my/myPageSideBar';

const API_BASE_URL = process.env.REACT_APP_API_URL || "http://13.208.47.23:8911/api";

const ManageUser = () => {
  const [allUsers, setAllUsers] = useState([]);
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  //  
  useEffect(() => {
    const token = Cookies.get('jwt_token');  

    if (token) {
        axios.get(`${API_BASE_URL}/userinfo`, { 
            headers: { 'Authorization': `Bearer ${token}` }, 
            withCredentials: true
        })
        .then(response => {
            setUser(response.data);  

            if (response.data.userauthor !== 0) {
                navigate('/login');
            }
        })
        .catch(error => {
            alert(error.response.data.message); 
            Cookies.remove('jwt_token');  
            navigate('/login');
        });
    } else {
        navigate('/login');  
    }
}, [navigate]);

  //   
  useEffect(() => {
    axios
      .get(`${API_BASE_URL}/getAllUsers`) 
      .then((response) => {
        setAllUsers(response.data);
      })
      .catch((error) => {
        console.error("  :", error);
      });
  }, []);

  // 
  const handleKick = (member_id, username) => {
    if (window.confirm(` ${username} ?`)) {
      const comment = prompt("  :");
      if (!comment) return;

      axios
        .post(`${API_BASE_URL}/kick`, null, {
          params: { member_id, member_kick_comment: comment },
        })
        .then(() => {
          alert("  ");
          window.location.reload();
        })
        .catch((error) => {
          console.error(error);
          alert("  !");
        });
      }
  };

  //  -> 1 
  const handleRestore = (member_id, username) => {
    if (window.confirm(` ${username}   ?`)) {

      axios
        .post(`${API_BASE_URL}/restore`, null, {
          params: { member_id, member_kick_comment: '' },
        })
        .then(() => {
          alert("  ");
          window.location.reload();
        })
        .catch((error) => {
          console.error(error);
          alert("  !");
        });
      }
  };


  return (
    <div className="mypage-form">
      <MyPageSideBar></MyPageSideBar>
      <div className="mypage-info"> 
        <div className="manage-table">
          <table border="1" style={{ borderCollapse: 'collapse', tableLayout: 'fixed', width: '100%', border: '1px solid rgb(209, 198, 198)'}}>
            <thead>
              <tr>
                <th style={{width:'6%'}}></th>
                <th style={{width:'10%'}}> </th>
                <th style={{width:'10%'}}> </th>
                <th style={{width:'8%'}}>ID</th>
                <th style={{width:'8%'}}></th>
                <th style={{width:'8%'}}></th>
                <th style={{width:'10%'}}></th>
                <th style={{width:'9%'}}></th>
                <th style={{width:'10%'}}></th>
                <th style={{width:'5%'}}> </th>
                <th style={{width:'10%'}}>/ </th>
                <th style={{width:'6%'}}></th>
              </tr>
            </thead>
            <tbody style={{height: '350px'}}>
              {allUsers.map((user) => (
                <tr key={user.member_id}>
                  <td>{user.userauthor === 0 ? '' : user.userauthor === 2 ? '' : ''}</td>
                  <td>{user.join_date}</td>
                  <td>{user.update_date}</td>
                  <td style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em', whiteSpace: 'nowrap'}}>{user.username}</td>
                  <td>{user.realname}</td>
                  <td style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em'}}>{user.nickname}</td>
                  <td style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em'}}>{user.email}</td>
                  <td>{user.phone}</td>
                  <td style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em'}}>{user.address}</td>
                  <td>{user.member_status}</td>
                  <td style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em'}}>{user.member_kick_comment}</td>
                  <td>
                      {user.member_status !== "" && (
                        <button className="positive-button" onClick={() => handleRestore(user.member_id, user.username)}></button>
                      )}
                      {user.member_status === "" && (
                        <button className="negative-button" onClick={() => handleKick(user.member_id, user.username)}></button>
                      )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default ManageUser;