import React, { useState, useEffect } from 'react';
import axios from 'axios';  
import { useNavigate } from 'react-router-dom';  
import Cookies from 'js-cookie'
import MyPageSideBar from '../my/myPageSideBar';

const API_BASE_URL = process.env.REACT_APP_API_URL || "http://13.208.47.23:8911/api";

const ManageSeller = () => {
  const [allBrands, setAllBrands] = useState([]);
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  //  
  useEffect(() => {
    const token = Cookies.get('jwt_token');  

    if (token) {
        axios.get(`${API_BASE_URL}/userinfo`, { 
            headers: { 'Authorization': `Bearer ${token}` }, 
            withCredentials: true
        })
        .then(response => {
            setUser(response.data);  

            if (response.data.userauthor !== 0) {
                navigate('/login');
            }
        })
        .catch(error => {
            alert(error.response.data.message); 
            Cookies.remove('jwt_token');  
            navigate('/login');
        });
    } else {
        navigate('/login');  
    }
}, [navigate]);

  //   
  useEffect(() => {
    axios
      .get(`${API_BASE_URL}/getAllBrands`) 
      .then((response) => {
        const sortedBrands = response.data;
        
        // " "   
        const submitBrands = sortedBrands.filter((brand) => brand.brand_status === " ");
        const acceptBrands = sortedBrands.filter((brand) => brand.brand_status === "");
        const refuseBrands = sortedBrands.filter((brand) => brand.brand_status === "");
        setAllBrands([...submitBrands, ...acceptBrands, ...refuseBrands]);
      })
      .catch((error) => {
        console.error("  :", error);
      });
  }, []);

  //   
  const handleAccept = (brand_id, username) => {
    if (window.confirm(` ${username}   ?`)) {
    axios
      .post(`${API_BASE_URL}/sellAccept`, null, {
        params: { brand_id },
      })
      .then(() => {
        alert("   ");
        window.location.reload();
      })
      .catch((error) => {
        console.error(error);
        alert("  !");
      });
    }
  };

  //   
  const handleRefuse = (brand_id, username) => {
    if (window.confirm(` ${username}   ?`)) {
      const comment = prompt("  :");
      if (!comment) return;

      axios
        .post(`${API_BASE_URL}/sellRefuse`, null, {
          params: { brand_id, brand_refusal_comment: comment },
        })
        .then(() => {
          alert("   ");
          window.location.reload();
        })
        .catch((error) => {
          console.error(error);
          alert("  !");
        });
      }
  };

  //   
  const handleCancel = (brand_id, username) => {
    if (window.confirm(` ${username}   ?`)) {
      const comment = prompt("   :");
      if (!comment) return;

      axios
        .post(`${API_BASE_URL}/sellRefuse`, null, {
          params: { brand_id, brand_refusal_comment: comment },
        })
        .then(() => {
          alert("   ");
          window.location.reload();
        })
        .catch((error) => {
          console.error(error);
          alert("   !");
        });
      }
  };

  return (
    <div className="mypage-form">
      <MyPageSideBar></MyPageSideBar>
      <div className="mypage-info">
        <div className="manage-table">
          <table border="1" style={{ borderCollapse: 'collapse', tableLayout: 'fixed', width: '100%', border: '1px solid rgb(209, 198, 198)'}}>
            <thead>
              <tr>
                <th style={{width:'10%'}}> </th>
                <th style={{width:'10%'}}> </th>
                <th style={{width:'8%'}}></th>
                <th style={{width:'10%'}}><br></br></th>
                <th style={{width:'10%'}}><br></br></th>
                <th style={{width:'10%'}}><br></br></th>
                <th style={{width:'8%'}}></th>
                <th style={{width:'8%'}}></th>
                <th style={{width:'5%'}}><br></br></th>
                <th style={{width:'5%'}}><br></br></th>
                <th style={{width:'5%'}}></th>
              </tr>
            </thead>
            <tbody style={{height: '350px'}}>
              {allBrands.map((brand) => (
                <tr key={brand.brand_number}>
                  <td>{brand.brand_join_date}</td>
                  <td>{brand.brand_update_date}</td>
                  <td>{brand.brandname}</td>
                  <td>{brand.brand_number}</td>
                  <td><img src={`${API_BASE_URL}/showimage?filename=${brand.brandlogo_url}&obj=brand`} alt=" " style = {{ width:'100%', objectFit: 'cover' }} /></td>
                  <td  style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em', whiteSpace: 'nowrap'}}>
                    <a href={`${API_BASE_URL}/downloadFile?filename=${brand.brandlicense_url}`} target="_blank" rel="noopener noreferrer">
                      {brand.brandlicense_url}
                    </a>
                  </td>
                  <td style={{maxHeight: '3em', overflow: 'auto', lineHeight: '1.5em', whiteSpace: 'nowrap'}}>{brand.username}</td>
                  <td>{brand.realname}</td>
                  <td><a href={`../../brand/${brand.brand_id}`}></a></td>
                  <td>{brand.brand_status}</td>
                  <td>
                    {brand.brand_status === " " && (
                      <>
                        <button className="positive-button" onClick={() => handleAccept(brand.brand_id, brand.username)}></button>
                        <button className="negative-button" onClick={() => handleRefuse(brand.brand_id, brand.username)}></button>
                      </>
                    )}
                    {brand.brand_status == "" && (
                        <button className="negative-button" onClick={() => handleCancel(brand.brand_id, brand.username)}></button>
                    )}
                    {brand.brand_status == "" && (
                        <button className="positive-button" onClick={() => handleAccept(brand.brand_id, brand.username)}></button>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          </div>
      </div>
    </div>
  );
};

export default ManageSeller;