import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; 
import '../../../styles/Join.css';

const Join = () => {
  const [agreeToTerms, setAgreeToTerms] = useState(false); 
  const navigate = useNavigate(); 

  const handleTermsChange = (e) => {
    setAgreeToTerms(e.target.checked);
  };

  const handleNextClick = () => {
    if (agreeToTerms) {
      navigate('/joinN'); 
      sessionStorage.setItem('joinValid', 'true');
    } else {
      alert('     .');
    }
  };

  const handlePrevClick = () => {
      navigate('/login');
  };

  return (
    <div className="join-term-container">
      <div className="join-flow">
        <div className="join-flow-title"></div>
        <div><span className="join-flow-content">01 </span>  02    03  </div>
      </div>
      <hr></hr>
      <div className="join-term">
        <div className="join-term-agree">
           <hr></hr>
        </div>
        <div className="join-term-content">
          <strong>1 ()</strong><br></br>
            ''       , ,   .<br></br>
          <br></br>
          <strong>2 ()</strong><br></br>
          1.     , , , , , .<br></br>
          2.          .<br></br>
          3.      ,         .<br></br>
          4.         ,               .<br></br>
          <br></br>
          <strong>3 ( )</strong><br></br>
          1.          .<br></br>
          2.         .<br></br>
          3.             .<br></br>
          4.             .<br></br>
          5.            ,        .<br></br>
          <br></br>
          <strong>4 ()</strong><br></br>
          1.       .<br></br>
          2.  ,    1    .<br></br>
          3.     1   .<br></br>
          4.     ,         .<br></br>
          &nbsp;&nbsp;&nbsp;&nbsp;-       <br></br>
          &nbsp;&nbsp;&nbsp;&nbsp;-        <br></br>
        </div>
      
        <div className="essential-terms">
          <input type="checkbox" id="terms" className="checkbox-join-custom" checked={agreeToTerms} onChange={handleTermsChange} />
          <label htmlFor="terms">
            <span className="required-text">[]</span> 
          </label>
        </div>
        <hr></hr>
        <div>
          <button onClick={handlePrevClick} className="prev-join">
            
          </button>
          <button onClick={handleNextClick} className="next-join">
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default Join;