import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; 
import '../../../styles/JoinN.css';

const JoinSuccess = () => {
  const navigate = useNavigate(); 

  const handleLoginClick = () => {
      navigate('/login'); 
  };

  const handleHomeClick = () => {
      navigate('/');
  };

  return (
    <div className="join-form-container">
      <div className="join-flow">
        <div className="join-flow-title"></div>
        <div>01   02    <span className="join-flow-content">03  </span></div>
      </div>
      <hr></hr>
      <div className="join-form">
        <div className="join-form-title">
           <hr></hr>
        </div>
        <div className="join-success-content">
          <div>
               !<br></br>
            <br></br>
          </div>
          <div>
               <span className="join-money">3000</span> .<br></br>
            <br></br>
               !<br></br>
            <br></br>
          </div>
          <hr></hr>
          <div className="join-button-form">
            <button onClick={handleHomeClick} className="prev-join">
              
            </button>
            <button onClick={handleLoginClick} className="next-join">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default JoinSuccess;