import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import Cookies from 'js-cookie';

const API_BASE_URL = process.env.REACT_APP_API_URL || "http://13.208.47.23:8911/api";

const MyOrder = () => {
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [productsInfo, setProductsInfo] = useState({});
    const [user, setUser] = useState({
        member_id: '',
        username: '',
        realname: '',
        nickname: '',
        phone: '',
        email: '',
        address: '',
        userauthor: ''
      });
    const navigate = useNavigate();

    const getTokenFromCookie = () => {
        return Cookies.get('jwt_token') || null;
    };

    const fetchProductDetails = async (productId) => {
        try {
            const response = await axios.get(`${API_BASE_URL}/item/${productId}`);
            return response.data;
        } catch (error) {
            console.error(` ID ${productId}   :`, error);
            return null;
        }
    };

    const handleReturnOrder = async (orderId) => {
        const token = getTokenFromCookie();
        if (!token) {
            setError(" .");
            return;
        }
        
        try {
            await axios.post(`${API_BASE_URL}/buy/return/${orderId}`, {}, {
                headers: { 'Authorization': `Bearer ${token}` },
                withCredentials: true
            });
            
            alert(" .");
            
            const updatedOrders = orders.map(order => {
                if (order.orderId === orderId) {
                    return { ...order, status: '' };
                }
                return order;
            });
            
            setOrders(updatedOrders);
        } catch (error) {
            console.error("   :", error);
            alert("    : " + (error.response?.data?.message || error.message));
        }
    };

    const handleStatusChange = async (id, newStatus) => {
        const token = getTokenFromCookie();
        if (!token) {
            setError(" .");
            return;
        }

        try {
            await axios.post(`${API_BASE_URL}/buy/change/${id}`, {}, {
                headers: { 'Authorization': `Bearer ${token}` },
                withCredentials: true,
                params: { status: newStatus}
            });

            setOrders((prevOrders) => 
                prevOrders.map((order) => order.orderId === id ? {...order, status: newStatus} : order)
            )
            
        } catch (error) {
            console.error("   :", error);
            alert("    : " + (error.response?.data?.message || error.message));
        }

    }

    useEffect(() => {
        const token = Cookies.get('jwt_token'); 
        
        if (token) {
          axios.get(`${API_BASE_URL}/userinfo`, { 
            headers: { 'Authorization': `Bearer ${token}` }, 
            withCredentials: true
          })
          .then(response => {
            setUser(response.data);
          })
          .catch(error => {
            alert(error.response.data.message);
            Cookies.remove('jwt_token');
            navigate('/login');
          });
        } else {
          navigate('/login');
        }
      }, [navigate]);

    useEffect(() => {
        const fetchOrders = async () => {
            const token = getTokenFromCookie();
            if (!token) {
                setError(" .");
                setLoading(false);
                return;
            }
            
            try {
                console.log("  API  ..."); 
                const response = await axios.get(
                    `${API_BASE_URL}/buy/my/order`, 
                    {
                        headers: {
                            'Authorization': `Bearer ${token}`
                        },
                        withCredentials: true
                    }
                );
                
                console.log("  :", response.data);
                
                if (!response.data || response.data.length === 0) {
                    console.log("  .");
                    setOrders([]);
                    setLoading(false);
                    return;
                }
                
                const processedOrders = response.data.map(order => {
                    try {
                        if (order.productInfo && typeof order.productInfo === 'string') {
                            try {
                                order.productInfo = JSON.parse(order.productInfo);
                            } catch (e) {
                                console.error("productInfo  :", e);
                                order.productInfo = [];
                            }
                        } else if (!order.productInfo) {
                            order.productInfo = [];
                        }
                        return order;
                    } catch (e) {
                        console.error(`   :`, e);
                        return order;
                    }
                });
                
                setOrders(processedOrders);

                const allProductIds = new Set();
                processedOrders.forEach(order => {
                    if (Array.isArray(order.productInfo)) {
                        order.productInfo.forEach(product => {
                            if (product.productId) {
                                allProductIds.add(product.productId);
                            }
                        });
                    }
                });
                
                const productInfoMap = {};
                for (const productId of allProductIds) {
                    const productDetail = await fetchProductDetails(productId);
                    if (productDetail) {
                        productInfoMap[productId] = productDetail;
                    }
                }
                
                setProductsInfo(productInfoMap);
                setLoading(false);
            } catch (err) {
                console.error("   :", err);
                setError("   .");
                setLoading(false);
            }
        };

        fetchOrders();
    }, []);

    if (loading) return (
        <div className="text-center mt-5">
            <div className="spinner-border text-primary" role="status"></div>
            <p className="mt-2"> ...</p>
        </div>
    );
    
    if (error) {
        return (
            <div className="container mt-5">
                <div className="alert alert-danger">{error}</div>
                <button className="btn btn-primary" onClick={() => navigate("/login")}>
                      
                </button>
            </div>
        );
    }

    if (orders.length === 0) {
        return (
            <div className="container mt-5">
                <h2>  </h2>
                <div className="alert alert-info">
                      .
                </div>
                <button className="btn btn-primary" onClick={() => navigate("/")}>
                     
                </button>
            </div>
        );
    }


    return (
        <div className="container mt-5">
            <h2 className="text-center fw-bold mb-4">  </h2>
            
            {orders.map((order, orderIndex) => {
                const productAmount = order.productInfo.reduce((sum, product) => sum + (product.price * product.quantity), 0);
                const finalAmount = order.totalAmount - (order.usedPoints || 0);

                return (
                    <div key={orderIndex} className="card mb-4">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <span>: {order.orderId || " "}                  
                                {user.userauthor == 2 &&(
                                    <select value={order.status} onChange={(e) => handleStatusChange(order.orderId, e.target.value)}>
                                        <option value=" "> </option>
                                        <option value=" "> </option>
                                        <option value=" "> </option>
                                        <option value=""></option>
                                    </select>
                                )}</span>
                            <span className={`badge ${
                                order.status === ' ' ? 'bg-warning' : 
                                order.status === ' ' ? 'bg-info' : 
                                order.status === ' ' ? 'bg-success' : 
                                order.status === '' ? 'bg-info' : 'bg-secondary'
                            }`}>
                                {order.status || "  "}
                            </span>
                        </div>
                        <div className="card-body">
                            <div className="mb-3">
                                <strong>:</strong> {order.orderDate ? new Date(order.orderDate).toLocaleString('ko-KR') : ' '}
                            </div>
                            
                            <div className="table-responsive mb-3">
                                <table className="table table-bordered">
                                    <thead>
                                        <tr>
                                            <th> </th>
                                            <th></th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Array.isArray(order.productInfo) && order.productInfo.length > 0 ? (
                                            order.productInfo.map((product, index) => {
                                                const productDetail = productsInfo[product.productId] || {};
                                                return (
                                                    <tr key={index}>
                                                        <td className="d-flex align-items-center">
                                                            {productDetail.image_url && (
                                                                <img 
                                                                    src={`${API_BASE_URL}/showimage?filename=${productDetail.image_url}&obj=product`} 
                                                                    alt={productDetail.name || ` ${product.productId}`} 
                                                                    className="me-3 rounded" 
                                                                    style={{ width: '60px', height: '60px', objectFit: 'cover' }}
                                                                />
                                                            )}
                                                            <div>
                                                                <div>{productDetail.name || ` ID: ${product.productId}`}</div>
                                                                {productDetail.brandname && <small className="text-muted">: {productDetail.brandname}</small>}
                                                            </div>
                                                        </td>
                                                        <td>{product.quantity}</td>
                                                        <td>{product.price ? `${product.price.toLocaleString()}` : '-'}</td>
                                                        <td>{product.price && product.quantity ? `${(product.price * product.quantity).toLocaleString()}` : '-'}</td>
                                                    </tr>
                                                );
                                            })
                                        ) : (
                                            <tr>
                                                <td colSpan="4" className="text-center">  </td>
                                            </tr>
                                        )}
                                    </tbody>
                                    <tfoot>
                                        <tr>
                                            <td colSpan="3" className="text-end"><strong> </strong></td>
                                            <td>{productAmount.toLocaleString()}</td>
                                        </tr>
                                        <tr>
                                            <td colSpan="3" className="text-end"><strong> </strong></td>
                                            <td>{(order.usedPoints || 0).toLocaleString()}</td>
                                        </tr>
                                        <tr>
                                            <td colSpan="3" className="text-end"><strong>  </strong></td>
                                            <td><strong>{finalAmount.toLocaleString()}</strong></td>
                                        </tr>
                                    </tfoot>
                                </table>
                            </div>
                            
                            <div className="row">
                                <div className="col-md-6">
                                    <h5> </h5>
                                    <p><strong> :</strong> {order.receiverName || ' '}</p>
                                    <p><strong>:</strong> {order.receiverPhone || ' '}</p>
                                    <p><strong>:</strong> {order.receiverEmail || ' '}</p>
                                    <p><strong>:</strong> {order.shippingAddress || ' '}</p>
                                    <p><strong> :</strong> {order.shippingMessage || ' '}</p>
                                </div>
                                <div className="col-md-6">
                                    <h5> </h5>
                                    <p><strong> :</strong> {order.paymentMethod || ' '}</p>
                                </div>
                            </div>
                            
                            <div className="d-flex justify-content-end mt-4">
                                {order.status === ' ' && user.userauthor == 1 && (
                                    <button 
                                        className="btn btn-danger"
                                        onClick={() => handleReturnOrder(order.orderId)}
                                    >
                                         
                                    </button>
                                )}
                            </div>
                        </div>
                    </div>
                );
            })}
            
            <button className="btn btn-primary mt-3" onClick={() => navigate("/")}>
                 
            </button>
        </div>
    );
};

export default MyOrder;